import axios from 'axios'; //   
import React, { useEffect } from 'react';
import styled from 'styled-components';
import logo from '../logo.svg';
import { Link, useNavigate, useLocation } from 'react-router-dom';
import { useSignup } from '../context/SignupContext';

const Signup = () => {
  const location = useLocation(); 
  const isEdit = location.pathname.includes('edit');
  const navigate = useNavigate();
  const { form, setForm } = useSignup();

  const handleChange = (e) => {
    const { name, value } = e.target;
    setForm(prev => ({ ...prev, [name]: value }));
  };

   const handlePostcode = () => {
    new window.daum.Postcode({
      oncomplete: function (data) {
        let addr = data.userSelectedType === 'R' ? data.roadAddress : data.jibunAddress;
        setForm(prev => ({
          ...prev,
          zipcode: data.zonecode,
          address: addr,
        }));
      },
    }).open();
  };


  const handleConfirm = async () => {
  const { name, phone, age, gender, address, email, password, confirmPassword } = form;

  if (!name.trim()) return alert(' .');
  if (!phone.trim()) return alert(' .');
  if (!age.trim()) return alert(' .');
  if (!gender.trim()) return alert(' .');
  if (!address.trim()) return alert(' .');

  if (!isEdit) {
      if (!email.trim()) return alert(' .');
      if (!password.trim()) return alert(' .');
      if (!confirmPassword.trim()) return alert('  .');
      if (password !== confirmPassword) return alert('  .');

    try {
      await axios.post('https://halpme.site/api/v1/auth/signup', {
        nickname: form.name,
        password: form.password,
        email: form.email,
        phoneNumber: form.phone,
        age: parseInt(form.age, 10),
        gender: form.gender === '' ? 'MALE' : 'FEMALE',
        address: {
          zipCode: form.zipcode,
          basicAddress: form.address,
          detailAddress: form.addressDetail,
          direction: form.direction
        },
        role: 'ROLE_MEMBER'
      });

      alert(' !');
      navigate('/');
    } catch (error) {
      console.error(' :', error.response?.data || error);
      alert(' .  .');
    }


  } else {
    //    API  
    alert(' !');
  }
};

  useEffect(() => {
    const savedAddress = localStorage.getItem('selectedAddress');
    if (savedAddress) {
      setForm(prev => ({ ...prev, address: savedAddress }));
      localStorage.removeItem('selectedAddress');
    }
  }, [setForm]);

  useEffect(() => {
  const script = document.createElement('script');
  script.src = '//t1.daumcdn.net/mapjsapi/bundle/postcode/prod/postcode.v2.js';
  script.async = true;
  document.body.appendChild(script);
}, []);


  return (
    <AppWrapper>
      <Content>
        <FormWrapper>
           {isEdit && (
    <HeaderRow>
      <BackIcon onClick={() => navigate(-1)}>
        <svg width="9" height="15" viewBox="0 0 9 15" fill="none" xmlns="http://www.w3.org/2000/svg">
          <path d="M7.89258 13.3027L1.89258 7.30273L7.89258 1.30273" stroke="#1E1E1E" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round" />
        </svg>
      </BackIcon>
      <Title> </Title>
    </HeaderRow>
  )}

  {!isEdit && <Title></Title>}

            <FormGroup>
            <Label></Label>
            <Input name="name" value={form.name} onChange={handleChange} placeholder=" " />
          </FormGroup>

          <FormGroup>
            <Label></Label>
            <Input name="phone" value={form.phone} onChange={handleChange} placeholder="010-1234-5678" />
          </FormGroup>

          <Row>
            <FormGroup style={{ flex: 1 }}>
              <Label></Label>
              <Input name="age" value={form.age} onChange={handleChange} placeholder="6 " />
            </FormGroup>

            <FormGroup>
              <Label></Label>
              <GenderRow>
                <GenderButton selected={form.gender === ''} onClick={() => setForm(prev => ({ ...prev, gender: '' }))}></GenderButton>
                <GenderButton selected={form.gender === ''} onClick={() => setForm(prev => ({ ...prev, gender: '' }))}></GenderButton>
              </GenderRow>
            </FormGroup>
          </Row>

          <FormGroup>
            <Label></Label>
            <AddressRow>
              <Input name="zipcode" value={form.zipcode || ''} readOnly placeholder="" />
              <DongButton type="button" onClick={handlePostcode}> </DongButton>
            </AddressRow>
          </FormGroup>

          <FormGroup>
            <Label></Label>
            <Input name="address" value={form.address || ''} readOnly placeholder=" " />
          </FormGroup>

          <FormGroup>
            <Label></Label>
            <Input name="addressDetail" value={form.addressDetail || ''} onChange={handleChange} placeholder=")    " />
          </FormGroup>

          <FormGroup>
            <Label> </Label>
            <Input name="direction" value={form.direction} onChange={handleChange} placeholder=")   ." />
          </FormGroup>

          {!isEdit && (
            <>
              <FormGroup>
                <Label></Label>
                <Input name="email" value={form.email} onChange={handleChange} placeholder=" " />
              </FormGroup>

              <FormGroup>
                <Label></Label>
                <Input type="password" name="password" value={form.password} onChange={handleChange} placeholder="........" />
              </FormGroup>

              <FormGroup>
                <Label> </Label>
                <Input type="password" name="confirmPassword" value={form.confirmPassword} onChange={handleChange} placeholder="........" />
              </FormGroup>
            </>
          )}

          <ConfirmButton onClick={handleConfirm}>{isEdit ? '' : ''}</ConfirmButton>

          {!isEdit && (
            <Footer>
               ? <BoldLink to="/"></BoldLink>
            </Footer>
          )}
        </FormWrapper>
      </Content>
    </AppWrapper>
  );
};

export default Signup;

const AppWrapper = styled.div`
  width: 100%;
  min-height: 100vh;
  display: flex;
  justify-content: center;
  background-color: #f5f5f5;
  padding: 32px 16px;
  box-sizing: border-box;
`;

const Content = styled.div`
  display: flex;
  flex-direction: column;
  gap: 24px;
  width: 100%;
  max-width: 360px;
  background-color: #FFFFFF;
  border-radius: 8px;
  box-shadow: 0 4px 10px rgba(0,0,0,0.1);
  margin: 0 auto;
`;

const Title = styled.h2`
  font-size: 24px;
  font-weight: 700; /* Bold */
  margin-bottom: 20px;
  color: #000000; /* Text_Main */
  text-align: left;
`;


const FormWrapper = styled.div`
  display: flex;
  flex-direction: column;
  gap: 16px;
  width: 100%;
  padding: 0 20px;
  box-sizing: border-box;
`;

const FormGroup = styled.div`
  display: flex;
  flex-direction: column;
  gap: 5px;
`;

const Label = styled.label`
  margin: 0px 10px;
  font-size: 14px;
  color: #000000;
  text-align: left;
  display: block;
`;
const DongButton = styled.button`
  padding: 8px 12px;
  font-size: 13px;
  border: 1px solid #2B9E90;
  background-color: #ffffff;
  color: #2B9E90;
  border-radius: 8px;
  cursor: pointer;
  white-space: nowrap;

  &:hover {
    background-color: #e6f7f5;
  }
`;

const Input = styled.input`
  width: 100%;
  height: 50px;
  padding-left: 10px;
  background: #F5F5F5;
  color: #000;
  border: 1px solid #ddd;
  border-radius: 8px;
  font-size: 14px;
  box-sizing: border-box;

  &:focus {
    outline: none;
    box-shadow: 0 0 0 2px #a0d9c7;
  }
`;

const Row = styled.div`
  display: flex;
  gap: 12px;
  width: 100%;
`;

const GenderRow = styled.div`
  display: flex;
  gap: 8px;
`;

const GenderButton = styled.button`
  padding: 10px 16px;
  border-radius: 50%;
  border: 1px solid #ccc;
  background: ${({ selected }) => (selected ? '#000' : '#fff')};
  color: ${({ selected }) => (selected ? '#fff' : '#000')};
  font-size: 14px;
  cursor: pointer;
`;

const AddressRow = styled.div`
  display: flex;
  align-items: center;
  gap: 8px;
`;

const ConfirmButton = styled.button`
  width: 100%;
  height: 50px;
  background-color: #2B9E90;
  color: white;
  margin-top: 32px;
  border: none;
  border-radius: 8px;
  font-size: 16px;
  cursor: pointer;

  &:hover {
    background-color: #3EC6B4;
  }
`;

const Footer = styled.div`
  text-align: center;
  margin: 24px 0;
  font-size: 14px;
  color: #888888;
`;

const BoldLink = styled(Link)`
  color: #000000;
  font-weight: bold;
  margin-left: 4px;
  text-decoration: none;
`;

const HeaderRow = styled.div`
  display: flex;
  align-items: center;
  gap: 8px;
`;

const BackIcon = styled.div`
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 24px;
  height: 24px;
`;

