import React, { useState, useEffect, useContext } from "react";
import styled from "styled-components";
import { AuthContext } from "../context/AuthContext";

import MobileLayout from "../components/MobileLayout";
import PostContent from "../components/PostContent";
import PostAuthorButtons from "../components/PostAuthorButtons";
import PostVisitorButtons from "../components/PostVisitorButtons";

const mockPosts = [
  {
    postId: 1,
    title: "  ",
    nickname: "",
    content: "   .         .      ",
    address: {
      zipCode: "12345",
      basicAddress: " ...",
      detailAddress: " 123",
      direction: "2 "
    },
    requestDate: "2025-05-26",
    requestTime: "10:00",
    date: "2025-05-26"
  },
];

const PostDetail = () => {
  const { user } = useContext(AuthContext);
  const [post, setPost] = useState(null);
  const [isAuthor, setIsAuthor] = useState(false);

  useEffect(() => {
      setPost(mockPosts[0]);
  }, []);

  useEffect(() => {
    if(user && post){
      setIsAuthor(user.nickname === post.nickname);
    }
    else {
      setIsAuthor(false);
    }
  }, [user, post]);

  return (
    <MobileLayout>
      <PostContent value={post} />

      {isAuthor 
        ? ( <PostAuthorButtons /> )
        : ( <PostVisitorButtons /> )
      }
    </MobileLayout>
  );
}

export default PostDetail;