import React, { useContext, useState } from "react";
import styled from "styled-components";
import axios from "axios";
import logo from "../assets/HalpmeLogo.svg";
import { Link } from "react-router-dom";
import { useNavigate } from "react-router-dom";
import facomment from "../assets/Kakao_Logo.png";
import { AuthContext } from "../context/AuthContext";

const Login = () => {
    const { login } = useContext(AuthContext);
    const navigate = useNavigate();

    const [email, setEmail] = useState('');
    const [pw, setPw] = useState('');

    //  
    const handleLogin = async () => {
      if (!email.trim() || !pw.trim()) {
        alert("   .");
        return;
      }


      try {
        const response = await axios.post("https://halpme.site/api/v1/auth/login", {
          email: email,
          pw: pw
        });

        //console.log("API :", response);          //    
        // console.log(" :", response.data);   //   

        if (response.data.success) {
          const token = response.data.data.token;
          login(token);
          alert(" ");
          navigate("/home");
        } 
        else {
          alert(" : " + response.data.message);
        }
      } 
      catch (error) {
        console.error(" :", error);
        alert("   .");
      }
    };

    // Enter   ( )
    const handleKeyDown = (e) => {
      if(e.key === 'Enter' )
        handleLogin();
    };

    //    
    const REST_API_KEY = "";
    const REDIRECT_URI = "";
    const kakaoLoginlink = `https://kauth.kakao.com/oauth/authorize?client_id=${REST_API_KEY}&redirect_uri=${REDIRECT_URI}&response_type=code`;

    const socialLoginHandler = () => {
      window.location.href = kakaoLoginlink;
    };

    return (
        <AppWrapper>
            <Content>
                <LogoWrapper>
                    <img 
                        src={logo}
                        alt="Halpme "
                    />
                </LogoWrapper>
                
                <FormWrapper onKeyDown={handleKeyDown}>
                    <FormGroup>
                        <Label></Label>
                        <Input 
                        type="email"
                        placeholder=" "
                        value={email}
                        onChange={(e) => setEmail(e.target.value)} 
                        />
                    </FormGroup>
                    <FormGroup>
                        <Label></Label>
                        <Input
                        type="password"
                        placeholder="" 
                        value={pw}
                        onChange={(e) => setPw(e.target.value)}
                        />
                    </FormGroup>

                    <LoginButton onClick={handleLogin}></LoginButton>
                    
                    <Text>
                         ? <BoldLink to="/signup"></BoldLink>
                    </Text>
                    
                    <ButtonContent>
                        <GuideText>   </GuideText>
                        <KakaoButton onClick={socialLoginHandler}>
                            <FacommentImg src={facomment} alt="" />
                            <span>  </span>
                        </KakaoButton>
                    </ButtonContent>
                </FormWrapper>
            </Content>
        </AppWrapper>
    );
}

export default Login;

//  
const AppWrapper = styled.div`
  width: 100%;
  height: 100%;
  min-height: 800px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #f5f5f5;
`;
const Content = styled.div`
  display: flex;
  flex-direction: column;
  gap: 24px;
  width: 100%;  
  height: 800px;
  max-width: 360px;
  background-color: #FFFFFF;
  box-shadow: 0 4px 10px rgba(0,0,0,0.1);
  text-align: center;
  position: relative;
  align-items: center;
`;

// 
const LogoWrapper = styled.div`
  display: flex;
  justify-content: center;

  img {
    margin: 42px;
    height: 200px;
    width: 200px;
  }
`;

//  (,  )
const FormWrapper = styled.div`
  display: flex;
  flex-direction: column;
  gap: 16px;
  width: 100%;
  padding: 0 20px;
  box-sizing: border-box;
`;

const FormGroup = styled.div`
  display: flex;
  flex-direction: column;
  gap: 5px;
`;

const Label = styled.label`
  margin: 0px 10px;
  font-size: 16px;
  color: #000000;
  text-align: left;
  display: block;
`;

const Input = styled.input`
  width: 100%;
  height: 50px;
  padding-left: 10px;
  margin: 0px auto;
  background: #F5F5F5;
  color: #888888;
  border: none;
  border-radius: 8px;
  font-size: 14px;
  box-sizing: border-box;

  &:focus {
    outline: none;
    box-shadow: 0 0 0 2px #a0d9c7;
  }
`;

const LoginButton = styled.button`
  width: 100%;
  height: 50px;
  background-color: #2B9E90;
  color: white;
  margin: 0px auto;
  border: none;
  border-radius: 8px;
  font-size: 16px;
  cursor: pointer;
  margin-top: max(48px, 2vh); 

  &:hover {
    background-color: #3EC6B4;
  }
`;

//     
const Text = styled.div`
  width: 39px
  font-size: 14px;
  margin: 5px;
  color: #888888;
`;

const BoldLink = styled(Link)`
  width: 76px;
  height: 39px;
  margin: 10px;
  font-size: 16px;
  font-weight: bold;
  color: black;
  text-decoration: none;
`;

//   
const ButtonContent = styled.div`
  display: flex;
  align-items: center;
  flex-direction: column;
  gap: 10px;
  margin-top: 48px;
`;

const GuideText = styled.p`
  color: #888888;
  font-size: 14px;
  margin: 0 auto
`;

const KakaoButton = styled.button`
  background-color: #fee500;
  color: #000;
  font-weight: bold;
  font-size: 16px;
  padding: 16px 0px;
  border: none;
  border-radius: 12px;
  width: 328px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  gap: 10px;
  .icon {
    font-size: 20px;
  }

  &:hover {
    opacity: 0.9;
  }
`;

const FacommentImg = styled.img`
  width: 24px;
  height: 24px;
`;
